<template>
  <div>
    <div class="content">
      <span class="iconfont icon-jianhao"
            @click="cratSub()"
            v-if='number.count'></span>
      <span class="num"
            v-if='number.count'>{{number.count}}</span>
      <span class="iconfont icon-jiahao"
            @click="cartAdd($event)"></span>
    </div>
  </div>
</template>

<script>
export default {
  props: ['index', "type"],
  computed: {
    // Ê≥®ÊÑèÊòØËÆ°ÁÆóÂ±ûÊÄßÔºå‰ºöÊõ¥Â•ΩÊñπ‰æøÂ±ûÊÄßÂèØËßÜÂåñ
    number () {
      // ËøîÂõûvuestoreListÁªÑ‰ª∂ÈáåÈù¢Êï∞ÊçÆlist[index][type]de ÂÄº
      return this.$store.state.storeList.list[this.index].content[this.type]
    }
  },
  methods: {
    cartAdd (e) {
      // ÊâæÂà∞store
      this.$store.commit('storeList/addCart', { index: this.index, type: this.type })
      this.$store.commit('ball/showBall', e.target)
    },
    cratSub () {
      this.$store.commit('storeList/subCart', { index: this.index, type: this.type })

    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  .iconfont {
    font-size: 0.38rem;
    padding: 0.18rem;
    vertical-align: hidden;
  }
  .icon-jianhao {
    color: #e2e3e5;
  }
  .icon-jiahao {
    color: #ffc134;
  }
  .num {
    font-size: 0.32rem;
  }
}
</style>