<template>
  <div>
    <div class="order">
      <div class="menu-list"
           ref="menu">
        <ul>
          <li class="menu"
              :class="{'active':activeIndex==index}"
              v-for="(obj,index) in list"
              :key="obj.id"
              @click='goToList(index)'>
            {{obj.name}}</li>
        </ul>
        <!-- Â∑¶‰æßÂ±±ÂïÜÂìÅÂàóË°® -->
      </div>
      <!-- Âè≥‰æßÂÜÖÂÆπÂàóË°® -->
      <div class="el-list"
           ref="elData">
        <ul>
          <!-- Á¨¨‰∏ÄÊ¨°Âæ™ÁéØÊ†áÈ¢ò -->
          <li class="el-title"
              v-for="(obj,index) in list"
              :key="index">
            <div class="title">{{obj.name}}</div>
            <ul>
              <!-- Âæ™ÁéØÂçï‰∏™ÂïÜÂìÅ -->
              <li class="el-box"
                  v-for="(item,Tindex) in obj.content"
                  :key="Tindex">
                <div class="img-box">
                  <img :src="item.img"
                       alt="">
                </div>
                <div class="info">
                  <div class="name">{{item.name}}</div>
                  <div class="sale">
                    <span class="num">{{item.num}}ÊúàÈîÄ</span>
                    <span class="up">{{item.up}}Ëµû</span>
                  </div>

                  <div class="price">¬• {{item.price}}</div>
                </div>
                <div class="bt-cart"></div>
                <addCart class="add-cart"
                         :index="index"
                         :type='Tindex'></addCart>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState } from 'vuex'
import addCart from '@/components/cart.vue'

export default {
  data () {
    return {
      menuScroll: null,
      elScroll: null,
      // ËÆæÂÆöÊîπÂèòmenuÊòØcssÁöÑÊ†áËÆ∞
      activeIndex: 0,
      // ÂàõÂª∫‰∏§‰∏™ÂÇ®Â≠òmenuÂíålistÁöÑÂàóË°®
      posy: [],//È´òÂ∫¶
      menuEl: [],//ÂÖÉÁ¥†
      // ‰øùÂ≠òÂΩìÂâçÊªëÂä®ÁöÑË∑ùÁ¶ª
      scrollY: 0
    }
  },
  methods: {
    // ÂàùÂßãÂåñÁªÑ‰ª∂
    initScroll () {
      this.menuScroll = new BScroll('.menu-list', {
        bounce: false,
      })
      this.elScroll = new BScroll('.el-list', {
        bounce: false,
        probeType: 3
      })
      // ËÆ°ÁÆóÂá∫listÁöÑÈ´òÂ∫¶
      this.getPosY()
      // ËÆ°ÁÆóÊªöÂä®Ë∑ùÁ¶ª
      this.menuEl = this.$refs.menu.getElementsByClassName('menu');
      // ÁõëÂê¨scrollÁªÑ‰ª∂ÁöÑÊªöÂä®ÔºåÊØèÊ¨°‰ºöÊääËøîÂõûÂÄºÂèñÊï¥ÁªôÊªöÂä®È´òÂ∫¶
      this.elScroll.on('scroll', e => {
        this.scrollY = Math.abs(Math.round(e.y));
      })
    },
    // ÁÇπÂáªËèúÂçïÊªöÂä®ÂàóË°®
    goToList (index) {
      let elList = this.$refs.elData.getElementsByClassName('el-title')
      // ÊãâÂèñÂÖ®ÈÉ®ÂïÜÂìÅÁ±ªËÄå‰∏çÊòØÊâÄÊúâÂïÜÂìÅ
      let el = elList[index]
      // ËøõË°ålistÁöÑÊªöÂä®ÔºåÂπ∂Êõ¥ÊîπÊ†áËÆ∞css
      this.elScroll.scrollToElement(el, 300)
      this.activeIndex = index
    },
    getPosY () {
      let elList = this.$refs.elData.getElementsByClassName('el-title')
      let y = 0;
      // Â∞ÜlistÁöÑÊØè‰∏™ÂïÜÂìÅÁ±ªÁöÑÈ´òÂ∫¶Ëé∑ÂèñÂπ∂‰øùÂ≠òÂú®ÂàóË°®
      for (let i = 0; i < elList.length; i++) {
        if (i == 0) this.posy.push(y)
        else {
          let upEl = elList[i - 1]
          y += upEl.offsetHeight
          this.posy.push(y)
        }
      }

    }
  },
  components: {
    addCart
  },
  computed: {
    ...mapState('storeList', ['list'])
    // Êò†Â∞ÑÂêéÂè∞Êï∞ÊçÆ
  },
  created () {
    // ËÆ©ÂêéÂè∞ÊâÄÊúâÊï∞ÊçÆÈÉΩÂä†ËΩΩÂ•ΩÂêéÂºÄÂßãËÆ°ÁÆóscrollÊèí‰ª∂ÊâÄÈúÄË¶ÅÁöÑÂÄº
    this.$store.dispatch('storeList/getListData', this.$route.query.id).then(() => {
      this.$nextTick(() => {
        this.initScroll()
      })
    })
  },
  watch: {
    // ÁõëÂê¨Â±ûÊÄßÔºåÊØèÊ¨°Â±ûÊÄßÊîπÂèò‰ºöËß¶Âèë
    scrollY (val) {
      for (let i = 0; i < this.posy.length; i++) {
        let x = this.posy[i]
        let y = this.posy[i + 1]
        if (val >= x && y > val) {
          let el = this.menuEl[i]
          this.menuScroll.scrollToElement(el, 300, 0, 100)
          // Ê≥®ÊÑèË¶ÅÊääÊ†áËÆ∞ÈáçÂÆö‰Ωç‰∏çÁÑ∂ÔºåÊ≤°ÂäûÊ≥ïÊõ¥ÊîπÊ†∑Âºè
          this.activeIndex = i
          // ËøîÂõûËäÇÁúÅÂÜÖÂ≠ò
          break
        }
      }
    }
  }
}

</script>

